body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #f4f4f4;
}
  
.App {
  text-align: center;
  padding: 20px;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
}  

header {
  background: #333;
  color: white;
  padding: 10px 0;
}

header nav {
  display: flex;
  justify-content: center;
  align-items: center;
}

header nav ul {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

header nav ul li {
  margin: 0 10px;
}

header nav ul li a {
  color: white;
  text-decoration: none;
}

.cart-icon {
  position: relative;
  display: inline-block;
}

.cart-icon span {
  background: red;
  border-radius: 50%;
  color: white;
  padding: 2px 6px;
  font-size: 12px;
  position: absolute;
  top: -10px;
  right: -10px;
}

input[type="text"] {
  width: 300px;
  padding: 10px;
  margin: 20px auto;
  border: 1px solid #ccc;
  border-radius: 4px;
  display: block;
}

h1 {
  text-align: center;
}

ul {
  list-style: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

li {
  border: 1px solid #c3c3c3;
  border-radius: 10px;
  margin: 10px;
  padding: 10px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

li img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
}

li div {
  text-align: center;
}

li h2 {
  font-size: 18px;
  margin: 10px 0;
}

li p {
  font-size: 16px;
  color: #333;
}

footer {
  background: #333;
  color: white;
  text-align: center;
  padding: 10px 0;
  position: fixed;
  width: 100%;
  bottom: 0;
}

.product {
  max-width: 800px;
  margin: auto;
  padding: 20px;
  text-align: center;
  background: white;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.product-image {
  max-width: 100%;
  max-height: 400px;
  margin-bottom: 20px;
}

button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 20px;
}

button:hover {
  background-color: #0056b3;
}

h2 {
  margin-top: 20px;
}

.cart-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
}

.cart-item img {
  margin-right: 20px;
}
